import * as yup from 'yup';

export const searchSchema = yup.object().shape({
  query: yup
    .string()
    .max(55, '  ')
    .trim()
    .test('len', ' ,   ', val => val.toString().length !== 0)
    .test(
      'len',
      '  ',
      val => val.toString().length !== 1 && val.toString().length !== 2,
    ),
});
